body {
    overflow-x: hidden;
}

.Article {
    .container {
        max-width: 1200px;
        width: 100%;
        margin-left: auto;
        margin-right: auto;
    }

    &-title {
        color: #80BC00;
        font-family: "Quicksand";
        margin-top: 150px;
        margin-bottom: 150px;
        display: flex;
        justify-content: center;

        @include mq() {
            margin-bottom: 0px;
        }
    }

    .proje {
        display: flex;
        flex-direction: column;
    }

    &-card {
        font-family: "Inter";
        max-width: 600px;
        width: 100%;
        height: 150px;
        background-color: rgba(#80BC00, .2);
        color: #80BC00;
        border-radius: 30px;
        display: flex;
        align-items: center;
        justify-content: center;
        padding: 20px 30px;
        text-align: center;
        margin-top: 50px;
        margin-bottom: 50px;

        @include mq() {
            display: flex;
            flex-direction: column;
        }
    }
}

.bir,
.üç,
.beş,
.yedi {
    @include mq() {
        display: flex;
        margin-left: auto;
        margin-right: auto;
    }
}

.iki,
.dört,
.altı,
.sekiz {
    display: flex;
    margin-right: 0;
    margin-left: auto;

    @include mq() {
        display: flex;
        margin-left: auto;
        margin-right: auto;
    }
}

.surec {
    display: flex;
    justify-content: center;
    align-items: center;
}


.surec-2 {
    display: flex;
    justify-content: center;
    align-items: center;
}

.panel1 {
    display: flex;
    width: 100%;
    position: relative;
    left: 130px;
}

.panel2 {
    display: flex;
    width: 100%;
    position: relative;
    left: 0px;

}

.surec1 {
    display: flex;
    justify-content: center;
    margin-left: auto;
    margin-right: auto;
    align-items: center;
    position: relative;
    width: 100%;

    @include mq() {
        display: none;
    }
}

.surec1::before {
    content: "";
    display: block;
    height: 200px;
    width: 10px;
    position: absolute;
    bottom: 40px;
    background-color: rgba(123, 152, 206, .45);

    @include mq() {
        width: 100%;
    }
}

.surec1::after {
    content: "";
    display: block;
    width: 100%;
    height: 10px;
    background-color: rgba(123, 152, 206, .45);


    @include mq() {}
}

.surec2 {
    display: flex;
    align-items: center;
    position: relative;
    width: 100%;
    max-width: 350px;

    @include mq() {
        display: none;
    }
}

.surec2::before {
    content: "";
    display: block;
    width: 50%;
    height: 10px;
    position: absolute;
    background-color: rgba(123, 152, 206, .45);
}

.surec2::after {
    content: "";
    display: block;
    height: 190px;
    width: 10px;
    position: absolute;
    left: 170px;
    bottom: 50px;
    z-index: -1;

    background-color: rgba(123, 152, 206, .45);

    @include mq() {
        width: 100%;
    }
}

.p-text {
    font-size: 0.9em;

    @include mq() {
        max-width: 800px;
        width: 100%;
        font-size: 0.9em;
    }
}

.özel::after {
    content: "";
    height: 0;
    width: 0;
}