.Header {
    overflow-x: hidden;



    &-inclusive {
        background: url("/img/arkaplan.png") center center no-repeat;
        background-size: cover;
        width: 100%;
        height: 100vh;
        overflow: hidden;
        position: relative;
        top: 0;
        left: 0;

        @include mq() {
            width: 100%;
            display: flex;
            align-items: center;
            justify-content: center;
        }
    }

    &-logo {
        position: absolute;
        left: 110px;
        top: 37px;

        @include mq() {
            position: absolute;
            left: 10px;
            top: 10px;
        }
    }

    &-title {
        width: 96%;
        height: 800px;
        display: flex;
        flex-direction: column;
        align-items: center;
        justify-content: center;

        &-svg {
            padding: 200px;

            @include mq() {
                width: 94%;
                padding: 20px;
                margin-left: 20px;
            }
        }
    }
}