.Aside {
    .container {
        position: relative;
        max-width: 1000px;
        width: 100%;
        display: flex;
        flex-direction: column;
        margin-left: auto;
        margin-right: auto;
    }

    &-title {
        color: #80BC00;
        font-family: "Quicksand";
        margin-top: 150px;
        margin-bottom: 50px;
        display: flex;
        margin-left: auto;
        margin-right: auto;
    }

    &-list {
        display: flex;
        font-family: "Inter";

        li {
            margin-left: 20px;
            margin-right: 20px;
            margin-bottom: 20px;
            margin-top: 20px;
            color: #737984;
        }
    }

    &-bg {
        position: relative;
        z-index: -1;
    }


    &-card {
        position: absolute;

        .simsek {
            position: relative;
            left: 100px;
            z-index: -1;

            @include mq() {
                display: none;
            }
        }

        .yaprak {
            position: relative;
            left: 600px;
            z-index: -1;

            @include mq() {
                display: none;
            }
        }

        &-pano {
            margin-top: 50px;
            border-radius: 30px;
            height: 300px;
            max-width: 600px;
            width: 100%;
            background-color: #7B98CE;
            display: flex;
            align-items: center;
            margin-left: auto;
            margin-right: auto;

            @include mq() {
                max-width: 600px;
                width: 100%;
                height: 200px;
            }

            &-text {
                color: white;
                font-size: 2em;
                text-align: center;

                @include mq() {
                    font-size: 1.5em;
                }
            }
        }
    }
}

.proje {

    &-title {
        color: #80BC00;
        font-family: "Quicksand";
        margin-top: 150px;
        margin-bottom: 150px;
        display: flex;
        justify-content: center;
    }

    .content {
        position: relative;
        display: flex;

        @include mq () {
            display: flex;
            flex-direction: column;
        }
    }

    .sarj {
        height: 350px;
        width: 250px;
        border: 5px solid #FDDF4D;
        border-radius: 30px;
        display: flex;
        justify-content: center;
        flex-direction: column;
        position: relative;
        margin-left: auto;
        margin-right: auto;

        @include mq () {
            margin-top: 20px;
        }

        select {
            color: #80bc00;
            border: 0;
            border-bottom: 2px solid #80bc00;
            width: 200px;
            display: flex;
            margin-left: auto;
            margin-right: auto;


        }
    }

    .title3 {
        color: #80BC00;
        text-align: center;
        width: 200px;
        text-align: center;
        margin-left: auto;
        margin-right: auto;
    }

    .button {
        height: 50px;
        width: 150px;
        border: 0;
        background-color: #80bc00;
        position: absolute;
        border-radius: 10px;
        color: white;
        font-family: "Inter";
        bottom: 10;
    }

    .buton {
        display: flex;
        justify-content: center;
    }
}

