.Section {
    &-area {
        margin-top: 95px;
        width: 100%;
        max-width: 1000px;
        margin-left: auto;
        margin-right: auto;
        display: flex;
        flex-direction: column;
        justify-content: center;
        align-items: center;


        @include mq() {
            max-width: 600px;
            width: 100%;
        }

        &-foto {
            display: flex;
            align-items: center;
            justify-content: center;
            position: relative;

            &-svg {

                .svg1 {
                    position: absolute;
                    bottom: 100px;
                    left: -80px;
                    z-index: -1;

                    @include mq() {
                        width: 73px;
                        height: 122px;
                        top: 20px;
                        left: -40px;
                        display: none;
                    }
                }

                .svg2 {
                    position: absolute;
                    left: 350px;
                    z-index: -1;

                    @include mq() {
                        width: 78px;
                        height: 123px;
                        top: 200px;
                        left: 400px;
                        display: none;
                    }
                }
            }

            &-img {
                margin-top: 60px;
                max-width: 464px;
                width: 100%;
                height: 261px;
                display: flex;
                position: relative;

                .resim {
                    width: 100%;

                    @include mq() {
                        width: 100%
                    }
                }
            }
        }
    }

    &-text {
        display: flex;
        flex-direction: column;

        @include mq() {
            padding-left: 20px;
            padding-right: 20px;
        }

        &-paragraph {
            color: #737984;
            text-align: center;
            font-family: 'Inter', sans-serif, Arial;

            @include mq() {
                text-align: left;
            }
        }
    }
}

.icon {
    position: absolute;
    left: 175px;
    top: 80px;
    height: 75px;
    width: 75px;
    background-color: rgba(238, 238, 238, 0.232);
    display: flex;
    justify-content: center;
    align-items: center;
    border-radius: 100px;
}

.simsek,
.yaprak,
.svg1,
.svg2 {
    opacity: 0.5;
}

h1 {
    font-size: 3.5em;
    text-align: center;
}