.Footer {
    margin-top: 200px;

    .container {
        display: flex;
        height: 250px;
        width: 100%;
        background-color: #2D2D2D;

        @include mq() {
            height: 600px;
        }

    }

    .content {
        max-width: 1200px;
        width: 100%;
        display: flex;
        flex-direction: column;
        justify-content: center;
        margin-left: auto;
        margin-right: auto;
    }

    .Footer-ground {
        display: flex;
        align-items: center;

        @include mq() {
            display: flex;
            flex-direction: column;
        }

        .yardim {
            max-width: 84px;
            width: 100%;
            padding-right: 50px;
            border-right: 2px solid white;

            @include mq () {
                border: 0;
                padding: 0;
                display: flex;
                margin-left: auto;
                margin-right: auto;
                margin-bottom: 50px;
                margin-top: 10px;
            }
        }

        .iyilik {
            padding-left: 50px;
        }

    }

    &-text {
        display: flex;
        flex-direction: column;
        margin-right: 0;
        margin-left: auto;
        gap: 15px;



        @include mq() {
            margin-top: 50px;
            display: flex;
            flex-direction: column;
            padding-left: 100px;
            gap: 15px;
            margin-left: auto;
            margin-right: auto;
        }

        a {
            text-decoration: none;
            color: white;

            &:hover {
                text-decoration: underline;
                color: #80BC00;
            }
        }
    }
}